.popup__field {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  height: 27px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 10px;
  padding-left: 0;
  box-sizing: border-box;
  width: 100%;
  outline: 0;
}

.popup__field::placeholder {
  font-size: 14px;
  line-height: 17px;
  color: #C4C4C4;
}